<script src="../../server/routes/users.js"></script>
<script src="../../server/routes/goods.js"></script>
<template>
	<div>
	<nav-header></nav-header>
	<div class="nav-breadcrumb-wrap">
        <div class="container">
          <nav class="nav-breadcrumb">
            <a href="/">Home</a>
            <span>Goods</span>
          </nav>
        </div>
      </div>
      <div class="accessory-result-page accessory-page">
        <div class="container">
          <div class="filter-nav">
            <span class="sortby">Sort by:</span>
            <a href="javascript:void(0)" class="default cur">Default</a>
            <a href="javascript:void(0)" class="price" @click="sortGoods">Price <svg class="icon icon-arrow-short"><use xlink:href="#icon-arrow-short"></use></svg></a>
            <a href="javascript:void(0)" class="filterby stopPop" @click.stop="showFilterPop">Filter by</a>
          </div>
          <div class="accessory-result">
            <!-- filter -->
            <div class="filter stopPop" id="filter" v-bind:class="{'filterby-show':filterBy}">
              <dl class="filter-price">
                <dt>Price:</dt>
                <dd><a href="javascript:void(0)"  @click="priceChecked=='all'">All</a></dd>
                <dd v-for="(price,index) in priceFilter">
                  <a href="javascript:void(0)" @click="setPriceFilter(index)" v-bind:class="{'cur':priceChecked==index}">{{price.startPrice}}-{{price.endPrice}}</a>
                </dd>

              </dl>
            </div>

            <!-- search result accessories list -->
            <div class="accessory-list-wrap">
              <div class="accessory-list col-4">
                <ul>
                  <li v-for="(item,index) in goodsList">
                    <div class="pic">
                      <a href="#">
                      	<!--v-bind:src-->
                      	<img v-lazy="'/static/'+item.prodcutImg" alt="">
                      </a>
                    </div>
                    <div class="main">
                      <div class="name">{{item.productName}}</div>
                      <div class="price">{{item.prodcutPrice}}</div>
                      <div class="btn-area">
                        <a href="javascript:;" class="btn btn--m">加入购物车</a>
                      </div>
                    </div>
                  </li>
                </ul>

              </div>
            </div>
          </div>
        </div>

      </div>
      <div class="md-overlay" v-show="overLayFlag" @click.stop="closePop"></div>
	<nav-footer></nav-footer>
	</div>
</template>

<script>
	import './../assets/css/base.css'
	import './../assets/css/product.css'
	import NavHeader from '@/components/NavHeader.vue'
	import NavFooter from '@/components/NavFooter.vue'
	import axios from 'axios'
	export default{
        data(){
            return {
           goodsList:[],
           sortFlag:true,
           page:1,
           pageSize:8,
           priceFilter:[
           {
           	startPrice:'0.00',
           	endPrice:'500.00'
           },
           {
           	startPrice:'500.00',
           	endPrice:'1000.00'
           },
           {
           	startPrice:'1000.00',
           	endPrice:'2000.00'
           }
           ],
           priceChecked:'all',
           filterBy:false,

           overLayFlag:false
            }
        },
        components:{
        	NavHeader,
        	NavFooter
        },
        mounted:function(){
        	this.getGoodsList();
        },
        methods:{
        	getGoodsList(){
            var param ={
                page:this.page,
                pageSize:this.pageSize,
                sort:this.sortFalg?1:-1
            }
        		axios.get("../static/goods.json",{
        		    param:param
            }).then((result)=>{
        			var res = result.data;
        			this.goodsList =res.result;
        		});
        	},
        	sortGoods(){
        	    this.sortFlag = ! this.sortFlag;
        	    this.page =1;
        	    this.getGoodsList();
          },
        	loadMore(){
        	  this.page++;
        	  this.busy = true;
        	  setTimeout(()=>{
        	   this.page++;
        	   this.getGood
            },1000);
          },
        	showFilterPop(){
        		this.filterBy = true;
        		this.overLayFlag = true;
        	},
        	setPriceFilter(index){
        		this.priceChecked = index;
        		this.closePop();
        	},
        	closePop(){
        		this.filterBy = false;
        		this.overLayFlag = false;
        	}
        }
}
</script>




